
<template>
  <div id="app">
    <div class="tabs">
      <ul>
          <router-link :to="{name:'Home'}" tag="li" active-class="active" >
            <div>
              <img/>
            </div>
            <div>首页</div>
          </router-link>
          <router-link :to="{name:'Explorer'}" tag="li" active-class="active" exact>
            <div>
              <img/>
            </div>
            <div>图书分类</div>
          </router-link>
        
          <router-link :to="{name:'Cart'}" tag="li">
            <div>
              <img/>
            </div>
            <div>购物车</div>
          </router-link>
        
          <router-link :to="{name:'Me'}" tag="li">
            <div>
              <img/>
            </div>
            <div>我</div>
          </router-link>
      </ul>
      <span>
          <router-link tag="li" :to="{name:'BookDetails',params:{id:1}}">
            <div>BookDetails</div><br>
          </router-link>
      </span>
    </div>
    <template>
      <transition name="slide-fade" mode="out-in">
        <router-view ></router-view>
      </transition>
    </template>
  </div>
</template>

<script>
export default {}
</script>

<style>
.slide-fade-enter-active{
  transition:all .3s ease;
}
.slide-fade-leave-active{
  transition:all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
  opacity:0;
}

.slide-fade-enter,.slide-fade-leave-active{
  transform:translateX(-430px);
  opacity:0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
li{
  display: inline-block;
  width: 20%;
  padding: 4px auto;
}
li:hover{
  background-color: aliceblue;
  color: #2c3e50;
}
.active{
  background-color: bisque;
}
</style>



