<template>
     <div>
       <div class="section">
         <div>
                    <!-- 热门推荐 -->
              <div class="swiper-container">
                  <div class="swiper-wrapper" v-for="slide in slides">
                      <router-link class="swiper-slide" tag="div" :to="{name:'BookDetails',params:{id:slide.id}}">
                        <img :src="slide.img_url" />
                      </router-link>
                  </div>
                  <!-- 如果需要分页器 -->
                  <div class="swiper-pagination"></div>
                  
                  <!-- 如果需要导航按钮 -->
                  <div class="swiper-button-prev"></div>
                  <div class="swiper-button-next"></div>
                  
                  <!-- 如果需要滚动条 -->
                  <div class="swiper-scrollbar"></div>
              </div>
                    <!-- 快讯 -->
              <div class="announcement">
                  <label >快讯</label>
                  <span>{{ announcement }}</span>
              </div>
         </div>
       </div>
       <div class="section">
                    <!-- 新手上架 -->
       </div>
       <div class="section">
                    <!-- 编辑推荐 -->
       </div>
     </div>
</template>

<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.css'


export default {
  data(){
    return{
      announcement:'今日上架图书全部8折！',
      slides:[
        {id:1,img_url:'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=28849038,472140465&fm=26&gp=0.jpg'},
        {id:2,img_url:'../assets/logo.png'}
      ]
    }
  },
  mounted(){
   new Swiper ('.swiper-container', {
    loop: true,
    // 如果需要分页器
    pagination: '.swiper-pagination',
    // 如果需要前进后退按钮
    nextButton: '.swiper-button-next',
    prevButton: '.swiper-button-prev',
    // 如果需要滚动条
    scrollbar: '.swiper-scrollbar',
  })
  }
}
</script>

<style>
#Home {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.swiper-container {
    max-width: 500px;
    max-height: 300px;
    min-height: 150px;
    margin: 20px auto;
}
</style>



